<template>
	<view class="page_forum" id="forum_index">
		<!-- 替换组件的搜索图标 -->
		<uni-search-bar placeholder="搜索帖子" @confirm="search" @cancel="cancel" cancelText="取消" @input="input($event, 'title')">
			<uni-icons slot="searchIcon" color="#999999" size="18" type="home" />
		</uni-search-bar>

		<list_forum style="background-color: #fff;" :list="list" class="mb"></list_forum>

		<uni-pagination class="pager" title="分页器" show-icon="true" :total="count" :pageSize="query.size" :current="query.page"
		 @change="page_change"></uni-pagination>
	</view>
</template>

<script>
	import mixin from "../../mixins/page.js"
	import list_forum from "../../components/diy/list_forum.vue"

	export default {
		mixins: [mixin],
		components: {
			list_forum
		},
		data() {
			return {
				url_get_list: "~/api/forum/get_list?",
				list: [],
				query: {
					title: "",
					page: 1,
					size: 10
				},
				count: 50
			}
		},
		methods: {
			page_change(e) {
				console.log(e);
			},
			input(e, key) {
				this.query[key] = e.value;
			},
			search() {
				this.query.page = 1;
				this.get_list();
			},
			cancel() {
				console.log("eer");
				this.query.title = "";
				this.search();
			}
		}
	}
</script>

<style scoped>
	.forum_list .chat-custom-right {
		font-size: 0.875rem;
		line-height: 1.5;
		text-align: right;
		color: #777;
	}

	.forum_list .praise {
		position: relative;
		top: 0.125rem;
	}

	.forum_list .praise::before {
		content: "";
		display: inline-block;
		width: 1rem;
		height: 1rem;
		background: url('/static/img/praise.png') 100% 100% no-repeat;
		position: relative;
		top: 0.125rem;
		margin-right: 0.25rem;
	}

	.forum_list .see {
		margin-left: 1rem;
		position: relative;
		top: 0.125rem;
	}

	.forum_list .see::before {
		content: "";
		display: inline-block;
		width: 1rem;
		height: 1rem;
		background: url('/static/img/see.png') 100% 100% no-repeat;
		position: relative;
		top: 0.125rem;
		margin-right: 0.25rem;
	}

	.forum_list .time {
		color: #999;
	}

	.pager {
		margin-top: 1rem;
	}
</style>
